@import 'cropper.scss';

.cropper-view-box,
.cropper-face {
  border-radius: 50%;
}
/* The css styles for `outline` do not follow `border-radius` on iOS/Safari (#979). */
.cropper-view-box {
  outline: 0;
  box-shadow: 0 0 0 1px #39f;
}
.image-cropper-container,
img {
  height: 200px;
  width: 200px;
  background: #fff;
}
